
.pricing h1{
    padding-top: 100px;
    padding-bottom: 100px;
    font-size: 5rem;
 text-align: center;
 font-family: 'Montserrat', sans-serif;
}
.all-card{
  
    display: flex;
    gap: 20%;
    margin-left: 16.5%;
    padding-bottom: 200px;
}
.all-card h1{
    font-size: 3rem;
}
.all-card button{
    margin-top: 50px;
}
.membership-card1{
    border: solid;
}
body{margin-top:20px;}
  .price-innerdetail h5 {
        font-weight: 400;
        letter-spacing: 2px;
        font-size: 15px;
    }
    
    .price-innerdetail p {
        font-size: 50px;
    }
    
    .detail-pricing {
        border-bottom: 1px solid;
        padding: 30px 0 30px 0;
    }
    
    .detail-pricing .float-left {
        padding: 0 0 0 40px;
    }
    
    .detail-pricing .float-left i {
        position: absolute;
        left: 0;
        font-size: 20px;
    }
    
    .detail-pricing span {
        display: inline-block;
        position: relative;
        font-weight: 400;
    }
    
    .wrap-price {
        background: rgba(32, 33, 36, .1);
        padding: 50px 20px 50px;
        border-radius: 10px;
    }
    
    .center-wrap {
        background: #070707;
        color: #fff;
    }
    @media (max-width:1000px) {
        .pricing h1{
            padding-top: 100px;
            padding-bottom: 100px;
            font-size: 2rem;
         text-align: center;
         font-family: 'Montserrat', sans-serif;
        }
    }