*{
    margin: 0px;
    padding: 0px;
}
html,body {
    margin:0;
    padding:0;
    overflow-x:hidden;
    }

.navbar-brand img{
    width: 70px;
}
.navbar-brand{
    display: flex;
    padding-left: 50px;
    
}
.navigatoin{
    position: fixed;
    z-index: 1;
}

.navbar-brand h1{
    padding-top: 3%;
    font-family: 'Poppins', sans-serif;
    letter-spacing: -1px;
    color: rgb(211, 207, 207)
}
.collapse ul{
    /* padding-top: 0px; */
    font-size: 1.1rem;
    padding-right: 50px;
}
.collapse ul li {
    font-family: 'Montserrat', sans-serif;
    padding-left: 10px; 
}
@media (max-width:400px) {
    .navbar-brand h1{
        padding-top: 3%;
        font-family: 'Poppins', sans-serif;
        letter-spacing: -1px;
        font-size: 1rem;
        color: rgb(211, 207, 207)
    }
    .navbar-brand img{
        width: 70px;
        display: none;
    }
}