.card-program{
    margin-top: 10%;
    max-width: 18rem;
    background-color: rgb(185, 185, 185);
    
    border-radius: 10px;
}
.programText h1{
    padding-top: 50px;
    text-align: center;
    color: white;
    text-decoration: underline;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 10px;
}
.program{
    display: flex;
    gap: 300px;
    padding-left: 220px;
}
.card img{
    max-width: 100%;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    
}


