.all-program{
    background-image: url("../../../public/pictures/test.jpg");
    background-size: 100% 100% ;
    padding-bottom: 100px;
}
.card{
    border: none;
    
}
@media (max-width:1000px) {
    .program{
        display: inline;
        
        gap: 300px;
        padding-left: 220px;
        text-align: center;
        padding-left: 10%;
    }
.card{
    margin-left: 35vw;
    
    
}
}
