.about{
    display: flex;
    gap: 10%;
    background-color: rgb(240, 236, 236)

}
.about-img img{
    
    width: 50vw;
    height: 100vh;
}
.about{
    z-index: -2;
    margin-top: 51.5%;
}
.about-text h1{
    padding-top: 50px;
    text-decoration: underline;
    font-family: 'Montserrat', sans-serif;
}

.about-content{
    padding-top:50px ;
}
.about-content h1{
    padding-top: 150px;
    text-decoration: none;
    /* text-decoration:overline; */
    
}
@media (max-width:1300px) {
    .about{
        display: inline;
        
        background-color: rgb(240, 236, 236)
    
    }
    .about-img img{
    
        width: 100%;
        height: 100%;   
    }
    .about-text{
        padding-left: 10%;
        padding-right: 10%;
        
    }
   

}






