.galary1-images img{
    width: 49vw;
}
.galary1-images{
    margin-left: 50.5%;
    
}
.galary1{
    display: flex;
    flex-direction: column-reverse;
   
}
.galary2-images img{
    width: 50vw;
}
.galary1-images{
    margin-left: 50.5%;
}
.galary2{
    display: flex;
    gap: 10%;
    background-color:rgb(228, 224, 224);
}
.galary2-text h1{
  padding-top: 50px;
  text-decoration: underline;
  font-family: 'Montserrat', sans-serif;
}
.galary2-text h3{
    padding-top: 50px;
    
}
@media (max-width:1000px) {
    .galary1-images{
        padding-top: 10%;
       margin-left:0px ;
    }
    .galary1-images img{
        width: 100%;
    }
    .galary2{
        display: inline;
        
        background-color:rgb(228, 224, 224);
    }
    .galary2-images img{
        width: 100%;
        padding-top: 10%;
    }
    .galary2-text h1{
        padding-top: 50px;
        text-decoration: underline;
        font-family: 'Montserrat', sans-serif;
        padding-left: 10%;
        padding-right: 10%;
      }
      .galary2-text h3{
        padding-top: 50px;
        padding-left: 10%;
        padding-right: 10%;
        padding-bottom: 10%;
        
    }
}