.carousel-item video{
    z-index: -1;
   object-fit: cover;
    width: 100vw;
    height: 100%;
    opacity: 40%;
    
}
.carousel-item{
     
     top: 0px;
     right: 0px;
     left: 0px;
     bottom: 0px;
   width: 100%;
   height: 100%;
   z-index: 1;
  background-color: rgb(0, 0, 0);
}
.main{
position:absolute;
margin-top: 0px;
z-index: 0;
}
.main h1{
    z-index: 1;
    position: absolute;
    top: 35%;
    left:20%;
    font-size: 5rem;
    color: rgb(236, 58, 58);
    font-family: 'Montserrat', sans-serif;
     font-weight: 700;
    letter-spacing: 10px;
    animation: fadeIn 6s;
}
@keyframes fadeIn{
    from{
        opacity: 0%;
    }
    to{
        opacity: 100%;
    }
}
.main h2{
    z-index: 1;
    position: absolute;
    top: 30%;
    left:20%;
    font-size: 3rem;
    color: rgb(226, 222, 222);
    font-family: 'Poppins', sans-serif;
    animation: fadeIn 3s
}
.main h3{
    z-index: 1;
    position: absolute;
    top: 44%;
    left:20%;
    font-size: 2rem;
    color: rgb(131, 128, 128);
    font-family: 'Montserrat', sans-serif;
    animation: fadeIn 3s;
}
.mainbtn1{
    position: absolute;
    top: 52%;
    left:20%;
    z-index: 1;
}
.mainbtn{
    position: absolute;
    top: 52%;
    left:28%;
    z-index: 1;
}
 
